.mapLoader {
  position: absolute;
  justify-self: center;
  align-self: center;
}

.available_country:hover {
  /*fill: orange;*/
  fill: orange !important;
  cursor: pointer;
}

#map path:hover {
  fill: #dadada;
}

path.active {
  fill: orange;
  /*fill: #1babec;*/
}
.background {
  fill: none;
  pointer-events: all;
}
.feature {
  fill: #ccc;
  cursor: pointer;
}

/*.feature.active {
          fill: orange;
        }*/
#map-holder {
  width: 100vw;
  height: 100vh;
}

.mesh {
  fill: none;
  stroke: #fff;
  stroke-linecap: round;
  stroke-linejoin: round;
}

#map-holder {
  position: relative;
  height: 100%;
  /*width: 100%;*/
  width: 100vw;
  max-width: 700px;
  overflow: hidden;
  display: grid;
  align-items: center;
  justify-items: center;
}

.selectCountry {
  position: relative;
  width: 100%;
  height: 100%;
  display: grid;
  align-items: center;
  justify-content: center;
  grid-template-rows: 25vh 1fr 1vh;
  /*background: #c1c1c1;*/
}

.SamericaContainer {
  position: relative;
  width: 100%;
  height: 100%;
  /*transform: scale(0);*/
}

.LoaderAppTittle {
  display: grid;
  align-items: center;
  justify-items: center;
}

.enableMap {
  /*opacity: 0;*/
  -webkit-animation-name: enableMap;
          animation-name: enableMap;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  /*animation-delay: .4s;*/
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(1, 1, 1, 1);
          animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@-webkit-keyframes enableMap {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes enableMap {
  0% {
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50% {
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  80% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

